import Bmob from "hydrogen-js-sdk";
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"

let that;
let uid;
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			account: "",
			Goods: null,
			reserve_num: 0,
			reserve_money: 0,
			
			sellRecord:{}
		}
	},

	onLoad(options) {
		that = this;
		uid = uni.getStorageSync("uid");
		that.account = uni.getStorageSync("account")
		
		console.log(options)
	},
	methods: {

		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.account)
					} else if (res.tapIndex == 1) {
						that.delete_this(that.account.objectId)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//编辑操作
		edit(item) {
			console.log(item)
			uni.setStorageSync("account", item);
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此账户及其相关的流水记录',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("accounts");
			query.destroy(id).then(res => {
				console.log(res)
				uni.navigateBack({
					delta:1
				})
				setTimeout(function(){
					uni.showToast({
						title: "删除成功",
						icon: "none"
					})
				},1000)
			}).catch(err => {
				console.log(err)
			})
		},
		
		//去编辑
		goto_edit() {
			uni.redirectTo({
				url: "../add/add"
			})
		},

		goto_detail(good) {
			uni.setStorageSync("now_product", good);
			uni.navigateTo({
				url: "/pages/manage/good_det/good_det"
			})
		},
	}
}
